import { supabase } from "../lib/supabase";
import { mockGigs, mockUsers } from "../data/mockData";

export const getGigs = async () => {
  try {
    const { data, error } = await supabase
      .from("gigs")
      .select(
        `
        *,
        users!inner(name, avatar)
      `,
      )
      .order("id");

    if (error || !data || data.length === 0) {
      // Fallback to mock data
      return mockGigs.map((gig) => {
        const mockUser = mockUsers.find((user) => user.id === gig.userId);
        return {
          ...gig,
          userName: mockUser?.name || gig.userName,
          userAvatar: mockUser?.avatar || gig.userAvatar,
          userId: gig.userId,
        };
      });
    }

    // Transform to match mock data structure
    return data.map((gig) => ({
      ...gig,
      userName: gig.users?.name || "Unknown User",
      userAvatar: gig.users?.avatar || "/default-avatar.jpg",
      userId: gig.user_id,
    }));
  } catch (error) {
    // Fallback to mock data
    return mockGigs.map((gig) => {
      const mockUser = mockUsers.find((user) => user.id === gig.userId);
      return {
        ...gig,
        userName: mockUser?.name || gig.userName,
        userAvatar: mockUser?.avatar || gig.userAvatar,
        userId: gig.userId,
      };
    });
  }
};

export const getGigById = async (id: string) => {
  try {
    const { data, error } = await supabase
      .from("gigs")
      .select(
        `
        *,
        users!inner(name, avatar)
      `,
      )
      .eq("id", id)
      .single();

    if (error || !data) {
      // Fallback to mock data
      const mockGig = mockGigs.find((gig) => gig.id === id);
      if (mockGig) {
        const mockUser = mockUsers.find((user) => user.id === mockGig.userId);
        return {
          ...mockGig,
          userName: mockUser?.name || mockGig.userName,
          userAvatar: mockUser?.avatar || mockGig.userAvatar,
          userId: mockGig.userId,
        };
      }
      throw new Error("Gig not found");
    }

    return {
      ...data,
      userName: data.users?.name || "Unknown User",
      userAvatar: data.users?.avatar || "/default-avatar.jpg",
      userId: data.user_id,
    };
  } catch (error) {
    // Fallback to mock data
    const mockGig = mockGigs.find((gig) => gig.id === id);
    if (mockGig) {
      const mockUser = mockUsers.find((user) => user.id === mockGig.userId);
      return {
        ...mockGig,
        userName: mockUser?.name || mockGig.userName,
        userAvatar: mockUser?.avatar || mockGig.userAvatar,
        userId: mockGig.userId,
      };
    }
    throw error;
  }
};

export const getUserById = async (id: string) => {
  const { data, error } = await supabase
    .from("users")
    .select("*")
    .eq("id", id)
    .single();

  if (error) {
    // If user not found in database, return a default user
    // This handles cases where user signed up but no record in users table
    return {
      id,
      name: "Unknown User",
      email: "unknown@example.com",
      avatar: "/default-avatar.jpg",
      bio: "User profile not available",
      skills: [],
    };
  }
  return data;
};

export const getMessages = async (userId: string) => {
  const { data, error } = await supabase
    .from("messages")
    .select(
      `
      *,
      sender:users!sender_id(name, avatar),
      receiver:users!receiver_id(name, avatar)
    `,
    )
    .or(`sender_id.eq.${userId},receiver_id.eq.${userId}`)
    .order("timestamp", { ascending: true });

  if (error) throw error;
  return data;
};

export const sendMessage = async (message: any) => {
  const { data, error } = await supabase
    .from("messages")
    .insert(message)
    .select()
    .single();

  if (error) throw error;
  return data;
};

export const createOrder = async (order: any) => {
  const { data, error } = await supabase
    .from("orders")
    .insert(order)
    .select()
    .single();

  if (error) throw error;
  return data;
};

export const getOrders = async (userId: string) => {
  const { data, error } = await supabase
    .from("orders")
    .select(
      `
      *,
      gigs!inner(title, price, user_id),
      users!sender_id(name, avatar)
    `,
    )
    .or(`buyer_id.eq.${userId},gigs.user_id.eq.${userId}`)
    .order("created_at", { ascending: false });

  if (error) throw error;
  return data;
};
